var iOSSplashScreen = (
  <skoash.Screen
    id="ios-splash"
    checkComplete={false}
    completeDelay={6000}
    showPrev={false}
  >
    <skoash.Image className="hidden" src="../shared/images/ios_start_ball.png" />
    <skoash.Image className="hidden" src="../shared/images/ios_start_ball_anim.gif" />
  </skoash.Screen>
);

export default iOSSplashScreen;



